<template>
	<div class="collection">
		<van-nav-bar title="我的收藏" fixed left-text="返回" @click-left="goMy" left-arrow />
		<Mymessage>
			<div class="collectionList">
				<ul>
					<li v-for="(item,index) in findAllLike" :key="index">
						<img class="auto-img" :src="item.smallImg" alt="">
						<div class="chName">{{item.name}}</div>
						<div class="enName">{{item.ebname}}</div>
						<div class="bottoms">
							<div class="price">￥{{item.price}}</div>
							<div class="icon" @click="deleteColl(item.pid)">
								<van-icon name="delete-o" color="#999"/>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</Mymessage>
		
	</div>
</template>

<script>
	import "../assets/less/Collection.less"
	import Mymessage from "../components/Mymessage.vue"
	export default {
		name: "PersonalData",
		data() {
			return {
				findAllLike:[],
				tokenString:""
			}
		},
		components: {
			Mymessage
		},
		created() {
			this.getFindLike()
		},
		methods: {
			goMy(){
				this.$router.go(-1)
			},
			//获取用户所有收藏商品
			getFindLike(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({name: "Login"})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/findAllLike',
					params: {
						tokenString: this.tokenString
				
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						return
				
					}
					if (res.data.code == 2000) {
						this.findAllLike=res.data.result
						// console.log(this.findAllLike);
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			
			//删除单个收藏商品
			deleteColl(pid){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'POST',
					url: '/notlike',
					data: {
						pid:pid,
						tokenString: this.tokenString,
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						return
				
					}
					if (res.data.code == 900) {
						this.getFindLike()
						this.$toast.loading({
							message: '删除成功',
							forbidClick: true,
							dutation: 500
						});
						
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			}
			
		},

	}
</script>

